export const SET_EPOCH_SIZE = Symbol('SET_EPOCH_SIZE');
export const SET_CURRENT_TIME = Symbol('SET_CURRENT_TIME');
export const SOFT_SET_CURRENT_TIME = Symbol('SET_CURRENT_TIME');
export const SHIFT_CURRENT_TIME = Symbol('SHIFT_CURRENT_TIME');

export const setEpochSize = epochSize => ({
  type: SET_EPOCH_SIZE,
  payload: epochSize,
});

export const setCurrentTime = currentTime => ({
  type: SET_CURRENT_TIME,
  payload: currentTime,
});

export const softSetCurrentTime = currentTime => ({
  type: SOFT_SET_CURRENT_TIME,
  payload: currentTime,
});

export const shiftCurrentTime = timeDelta => ({
  type: SHIFT_CURRENT_TIME,
  payload: timeDelta,
});

// Define what should escape rollup tree-shaking, since we package preserved
// file structure to npm.
//
// NODE_ENV condition is for consumer app: it should be able to tree-shake
// whatever it wants.
if (process.env.NODE_ENV === 'development') {
  const externalAPI = {
    SET_EPOCH_SIZE,
    SET_CURRENT_TIME,
    SOFT_SET_CURRENT_TIME,
    SHIFT_CURRENT_TIME,

    setEpochSize,
    setCurrentTime,
    softSetCurrentTime,
    shiftCurrentTime,
  };

  window &&
    window['This function does not exits'] &&
    window['This function does not exits'](externalAPI);
}
